<template>
  <div>
    <div class="user_top_bar">
      <div class="user_bar_bg">
        <div class="container clearfix">
          <div class="user_info pl_3">
            <a href='http://shangrongfintech.com/user/person'>
              <img class="user_pic" src="http://thirdwx.qlogo.cn/mmopen/iaaDjDDRGuZ8WzplG9BiaZkAyQxcaQOqD77WgyeTyvmTenJCaoHPwakUEaSkAKa9HBHpgbdmZvhwk19CeSE9Ah18ibE88Iye2qp/132" alt="">
            </a>
            <span class="user_name pl_2">
              <p class="name"> 小明</p>
              <p class="rank"> 客户 </p>

            </span>
            <i onclick='history.back() ' class="icon_right pr_3">
              <img src="http://cdn.shangrongfintech.com/static/img/user/bai.png" alt="">
            </i>

          </div>

        </div>
      </div>
    </div>
    <div class="amount">
      <div class="bg_bar">
        <div class="mum">
          <p>0</p>
          <p>累计订单金额(元)</p>
        </div>
      </div>
      <div class="icon_right">
        <img src="http://cdn.shangrongfintech.com/static/img/user/bai2.png" alt="">
      </div>
    </div>
    <div class="accout_box ">
      <h3 class="account_head circle_green">
        我的资产
      </h3>
      <div class="accout_body">
        <ul>
          <li>
            <a href="#">
              <div class="accout_main">
                <span class="accout_icon kehujl"></span>
                我的客户经理
              </div>

            </a>

          </li>
          <li>
            <a href="#">
              <div class="accout_main">
                <span class="accout_icon yuyuejl"></span>
                预约记录
              </div>
              <div class="count">
                共
                <span> 3 </span>次
              </div>
            </a>

          </li>

        </ul>

      </div>
    </div>
    <div class="accout_box ">
      <h3 class="account_head circle_green">
        记录中心
      </h3>
      <div class="accout_body">
        <ul>
          <li>
            <a href="#">
              <div class="accout_main">
                <span class="accout_icon changpin"></span>
                产品
              </div>
              <div class="count">
                浏览
                <span> 3 </span>次

              </div>
              <div class="count">
                转发
                <span> 3 </span>次

              </div>
            </a>

          </li>
          <li>
            <a href="#">
              <div class="accout_main">
                <span class="accout_icon zixun"></span>
                资讯
              </div>
              <div class="count">
                浏览
                <span> 300 </span>次
              </div>
              <div class="count">
                转发
                <span> 31 </span>次
              </div>
            </a>

          </li>

        </ul>

      </div>
    </div>
    <div class="accout_box ">
      <h3 class="account_head circle_green">
        账号管理
      </h3>
      <div class="accout_body">
        <ul>
          <li>
            <a href="#">
              <div class="accout_main">
                <span class="accout_icon zhanghao"></span>
                账号设置
              </div>

            </a>

          </li>
          <li >
            <a href="#">
              <div class="accout_main">
                <span class="accout_icon xinxipl"></span>
                信息披露
              </div>

            </a>

          </li>

        </ul>

      </div>
    </div>
    <div class="stop">
      <span class="line">我是有底线的</span>
    </div>
  </div>

</template>
<script>
export default {
  name: "user",
  data() {
    return {};
  }
};
</script>
<style lang="scss">
.pr_2 {
  padding-right: 2%;
}
.pl_2 {
  padding-left: 2%;
}
.pr_3 {
  padding-right: 3%;
}
.pl_3 {
  padding-left: 3%;
}
.clearfix::after {
  content: "";
  visibility: hidden;
  clear: both;
  display: block;
  height: 0;
}
.user_top_bar {
  .user_bar_bg {
    height: 118px;
    background: url(http://cdn.shangrongfintech.com/static/img/user/bg.png)
      no-repeat bottom left/cover;
    .user_info {
      padding-top: 20px;
      box-sizing: border-box;
      float: left;
      width: 100%;
      .user_pic {
        float: left;
        width: 58px;
        height: 58px;
        border-radius: 50%;
        overflow: hidden;
        border: 3px solid #fff;
      }
      .user_name {
        padding-left: 10px;
        padding-top: 10px;
        float: left;
        color: #fff;
        .name {
          font-size: 16px;
        }

        .rank {
          font-size: 10px;
        }
      }
    }
    .icon_right {
      float: right;
      padding: 20px;
      width: 12px;
      height: 20px;
      img {
        width: 100%;
      }
    }
  }
}
.amount {
  position: relative;
  padding: 10px 0;
  background-color: #f6f6f6;

  .mum {
    text-align: center;
  }
  .icon_right {
    position: absolute;
    right: 20px;
    top: 20px;
    text-align: right;
  }
}
.circle_green::before {
  content: "";
  position: absolute;
  display: block;
  background-color: #0ecfda;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  left: 20px;
  top: 10px;
}
.accout_box {
  border-top: 1px solid #e6e6e6;
  position: relative;
  background-color: #fff;
  margin-top: 10px;
  .account_head {
    padding-left: 35px;
    font-size: 16px;
    font-weight: normal;
    color: #666;
    line-height: 30px;
  }
  .accout_body {
    border-top: 1px solid #e6e6e6;
    background-color: #fff;
  }
  ul {
    display: flex;
    padding: 10px 0;
    justify-content: space-between;
    li {
      padding: 0;
      width: 50%;
    }
    li + li {
      border-left: 1px solid #e6e6e6;
    }
    a {
      display: block;
      padding: 0 20px;
      font-size: 16px;
      line-height: 1.6;
      color: #333;
      span {
        color: #ff5454;
      }
    }
    .count {
      text-align: center;
    }
  }
}
.accout_icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: middle;
}
.kehujl {
  background: url(http://cdn.shangrongfintech.com/static/img/user/licshi.png?v=6)
    no-repeat center/cover;
}
.yuyuejl {
  background: url(http://cdn.shangrongfintech.com/static/img/user/yuyue.png?v=3)
    no-repeat center/cover;
}
.changpin {
  background: url(http://cdn.shangrongfintech.com/static/img/user/cpin.png?v=3)
    no-repeat center/cover;
}
.zixun {
  background: url(http://cdn.shangrongfintech.com/static/img/user/zixu.png?v=3)
    no-repeat center/cover;
}
.zhanghao {
  background: url(http://cdn.shangrongfintech.com/static/img/user/shezhi.png?v=3)
    no-repeat center/cover;
}
.xinxipl {
  background: url(http://cdn.shangrongfintech.com/static/img/user/pilu.png?v=3)
    no-repeat center/cover;
}
.stop{
  padding-top:50px;
  color:#a7a7a7;
  height: 50px;
  text-align: center;
  .line{
    display:inline-block;
    position: relative;
   &::before{
     position: absolute;
    left: -100px;
    top: 50%;
      content:"";
    display: block;
    height:1px;
    width:90px;
    background-color: #d4d4d4;
   }
   &::after{
     position: absolute;
     top:50%;
     right:-100px;
         content:"";
    display: block;
    height:1px;
    width:90px;
    background-color: #d4d4d4;
   }
  }
}
</style>


