            </p>
            <p class="p2 main_font_Color">{{safe1.content}}</p>
          </a>
        </li>

      </ul>
    </Infobox>

    <Infobox>
        <aside slot="head_right" class="head_right">
            <a href="#">
                查看更多&nbsp;&gt;
            </a>
        </aside>
      <aside slot="head_slot" class="head_left blc_yellow">
        尚融精选：聊投资
      </aside>
      <ul slot="body_slot" class="news">
        <li v-for="news in news" :key="news.id">
          <a class="news_item" :href="news.url">
            <div class="message">
              <p>{{news.message}}
                <div class="message_bottom">
                  <div class="message_date">
                    {{ news.date}}
                  </div>
                  <div class="message_look">
                    <img src="http://cdn.shangrongfintech.com/static/index/images/browse.png" alt="">
                    <span class="count">
                      {{news.count}}

                    </span>
                  </div>
                </div>
            </div>
            <div class="pic">
              <img :src="news.pic" alt="">
            </div>
          </a>
        </li>
      </ul>
    </Infobox>

    <router-view/>
  </div>
</template>
