<style lang="scss">
$green: #54c5ff;
$yellow: #ff9600;
$red: #f52323;
$blue: #00aeff;
$white: #fff;
.blue {
  color: $blue !important;
}
.notice {
  background: $white;
  height: 62px;
  display: flex;
  justify-content: space-around;
  .notice_data {
    color: #7a7a7a;
    font-size: 12px;
    width: 50%;
    text-align: center;
    margin: 14px 0 16px;

    h2 {
      margin: 0;
      color: #ff5454;
      font-size: 14px;
    }
    p {
      display: inline-block;
      margin: 0;
      color: #7a7a7a;
      font-size: 12px;
      text-align: center;
    }
  }
}
.notice_data + .notice_data {
  border-left: 1px solid #ebebeb;
}
.goods {
  text-align: center;
  font-size: 14px;
  color: #666;
  display: flex;
  background-color: $white;
  border-top: 1px solid #eee;
  margin-top: 1px;
  padding: 10px 0;
  justify-content: space-around;
  .goods_wrap {
    width: 33.3%;
  }
  a {
    display: inline-block;
  }
  .goods_icon {
    width: 49px;
    height: 49px;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  p {
    color: #888;
  }
}
.marquee {
  background-color: $white;
  height: 44px;
  width: 100%;
  margin: 10px 0 15px;
  position: relative;
  .marquee_icon {
    height: 44px;
    background-color: $white;
    width: 30px;

    img {
      margin-top: 10px;
      margin-left: 10px;
      width: 20px;
    }
  }
  .marquee_content {
    position: absolute;
    left: 40px;
    bottom: 8px;
    marquee {
      font-size: 14px;
      span {
        color: #ea5555;
      }
    }
  }
}

.blc_red {
  border-left-color: $red;
}
.blc_yellow {
  border-left-color: $yellow;
}
.blc_green {
  border-left-color: $green;
}
.mb_10 {
  margin-bottom: 10px;
}
.loan {
  margin-bottom: 10px;
  li {
    height: 110px;
    padding: 0 10px;
    border-top: 1px solid #eee;
    a {
      display: block;
    }
    .top {
      display: flex;
      margin-top: 12px;
      padding-bottom: 13px;
      box-sizing: content-box;
      justify-content: space-between;
      .loan_icon {
        width: 50%;
      }
      img {
        width: 25%;
        height: 25px;
        vertical-align: top;
      }
      .company {
        color: #000;
        font-size: 14px;
      }
      .classify {
        line-height: 18px;
        border-radius: 4px;
        padding: 0 5px;
        border: 1px solid #ef5d5d;
        margin-left: 1%;
        text-align: center;
        font-size: 12px;
        color: #ef5d5d;
      }

      .time {
        color: $blue;
        text-align: right;
        font-size: 12px;
        b {
          font-weight: normal;
          margin: 0 2px;
        }
      }
    }
    .bottom {
      display: flex;
      justify-content: space-between;
      strong {
        color: #eb0101;
        font-size: 22.5px;
        font-weight: normal;
      }
      p {
        color: #888;
        font-size: 12px;
      }
      span {
        color: #333;
        font-size: 15px;
      }
    }
    .loan_button {
      width: 40px;
      height: 40px;
      background-color: #66e1d9;
      border-radius: 50%;
      border: 5px solid #d1f6f4;
      color: $white;
      line-height: 40px;
      text-align: center;
      font-size: 12px;
    }
  }
}
.safe {
  display: flex;
  justify-content: space-between;
  padding: 14px 10px 14px 10px;
  border-top: 1px solid #eee;
  li {
    width: 48%;
    .p1 {
      margin-top: 6px;
    }
    .p2 {
      line-height: 22px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      color: #888;
      font-size: 12px;
    }
    b {
      font-size: 14px;
      color: #333333;
    }
    span {
      display: inline-block;
      width: 57px;
      height: 20px;
      line-height: 18px;
      text-align: center;
      border-radius: 2px;
      border: solid 1px #ef5d5d;
      color: #ef5d5d;
      font-size: 11px;
    }
    a {
      display: block;
      img {
        height: 88px;
        width: 100%;
      }
    }
  }
}
.news {
  padding: 0 10px;
  li {
    border-top: 1px solid #eee;
    font-size: 12px;
    padding: 15px 0;
    .message {
      width: 65.5%;
    }
    p {
      font-size: 14px;
      word-break: break-all;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      color: #333333;
    }
    .message_bottom {
      display: flex;
      margin-top: 15px;
      justify-content: space-between;
    }
    .message_date {
      color: #888;
    }
    .message_look {
      margin-right: 10px;
      img {
        width: 14px;
        height: 8px;
      }
    }
    .pic {
      text-align: right;
      width: 29.5%;
      img {
        width: 100%;
        height: 71.5px;
      }
    }
    .news_item {
      display: flex;
      justify-content: space-between;
    }
  }
}
</style>




