<style lang="scss">
$green: #54c5ff;
$yellow: #ff9600;
$red: #f52323;
$blue: #00aeff;
$white: #fff;
.product_top_tab {
  background-color: #fff;
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
  ul {
    display: flex;
    padding: 10px 0 10px 0;
    justify-content: space-around;
    li {
      text-align: center;
      width: 25%;
      color: #999999;
      font-size: 14px;
      span {
        position: relative;
        padding-bottom: 5px;
      }
      .active {
        font-size: 16px;
        color: #66e1d9;
        &::after {
          position: absolute;
          bottom: -3px;
          left: 18px;
          content: "";
          width: 30px;
          height: 3px;
          background-color: #66e1d9;
          border-radius: 1.5px;
        }
      }
    }
  }
}

.product_content {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
  background-color: #fff;
  text-align: center;
}

.loan {
  margin-bottom: 10px;
  li {
    height: 110px;
    padding: 0 10px;
    border-top: 1px solid #eee;
    a {
      display: block;
    }
    .top {
      display: flex;
      margin-top: 12px;
      padding-bottom: 13px;
      box-sizing: content-box;
      justify-content: space-between;
      .loan_icon {
        width: 100%;
        text-align: left;
      }
      img {
        width: 25%;
        height: 25px;
        vertical-align: top;
      }
      .company {
        color: #000;
        font-size: 14px;
        display: inline-block;
        text-overflow: ellipsis;
        overflow: hidden;
        width: 25%;
        white-space: nowrap;
      }
      .classify {
        line-height: 18px;
        border-radius: 4px;
        padding: 0 5px;
        border: 1px solid #ef5d5d;
        margin-left: 1%;
        text-align: center;
        font-size: 12px;
        color: #ef5d5d;
      }
      .classify1 {
        border: 1px solid #ffb508;
        color: #ffb508;
      }
      .time {
        color: $blue;
        text-align: right;
        font-size: 12px;
        b {
          font-weight: normal;
          margin: 0 2px;
        }
      }
    }
    .bottom {
      display: flex;
      justify-content: space-between;
      strong {
        color: #eb0101;
        font-size: 22.5px;
        font-weight: normal;
      }
      p {
        color: #888;
        font-size: 12px;
      }
      span {
        color: #333;
        font-size: 16px;
      }
    }
    .loan_button {
      width: 40px;
      height: 40px;
      background-color: #66e1d9;
      border-radius: 50%;
      border: 5px solid #d1f6f4;
      color: $white;
      line-height: 40px;
      text-align: center;
      font-size: 12px;
    }
  }
}

.stop {
  padding-top: 50px;
  color: #a7a7a7;
  text-align: center;
  .line {
    display: inline-block;
    position: relative;
    &::before {
      position: absolute;
      left: -100px;
      top: 50%;
      content: "";
      display: block;
      height: 1px;
      width: 90px;
      background-color: #d4d4d4;
    }
    &::after {
      position: absolute;
      top: 50%;
      right: -100px;
      content: "";
      display: block;
      height: 1px;
      width: 90px;
      background-color: #d4d4d4;
    }
  }
}
</style>


