<template>
    <div class="my_tab_bar">
        <router-link v-for="item in tab_list" class="tabbar_item"  :key="item.id" :to="item.path">
            <!-- <div class="tab_icon"> <img :src="item.Img" alt=""> </div> -->
            <i class="iconfont"  :class="['icon-'+item.class1]"   ></i>
            <p class="tabbar__label">{{item.title}} </p>
        </router-link>
    </div>
</template>
<script>

export default {
  name: "tabbar",
  data() {
    return {
      tab_list:[
        { 
          "title":"首页",
          "path":"/",
          "class":"shouye",
          "class1":"weibiaoti1",

          "Img":"http://cdn.shangrongfintech.com/static/images/icon_nav_button.png?v=2"
      },{ 
          "title":"产品",
          "class":"wodechanpin",
          "class1":"msnui-product",
          "path":"/pages/product",
          "Img":"http://cdn.shangrongfintech.com/static/images/icon_nav_pro_off.png?v=3"
      },{ 
          "title":"我的",
           "class":"wode1",
           "class1":"wodemian2",
          "path":"/pages/user",
          "Img":"http://cdn.shangrongfintech.com/static/images/icon_nav_cell.png?v=3"
      }

      ]
    };
  }
};
</script>
<style lang="scss">
.my_tab_bar {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-around;
  left: 0;
  z-index: 999999;
  bottom: 0;
  margin-top: 5px;
  background: #fff;
  border-top: 1px solid #f2f2f2;

  .tabbar_item {
    padding: 7px 0 2px;
    font-size: 0;
    color: #999;
    text-align: center;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    .tab_icon,.iconfont {
      display: inline-block;
      font-size: 30px;
    }
  }
  .tabbar__label {
    text-align: center;
    color: #999;
    font-size: 10px;
    line-height: 1.8;
    padding-top: 6px;
    margin: 0;
   
  }
.exact p, .exact .iconfont{
     color: #65cfc9;
    }
  img {
    width: 100%;
  }
}


</style>



