<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <swiper :options="swiperOption" ref="mySwiper">
        <!-- 幻灯内容 -->
        <swiper-slide :key="i" v-for="(str, i) in test">
          <img :src="str.room_src" style="height:100%"/>
        </swiper-slide>
        <!-- 以下控件元素均为可选 -->
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script >

  import { swiper, swiperSlide } from 'vue-awesome-swiper'
 import 'swiper/dist/css/swiper.css'

  export default {
    props: [
        
    ],
    data () {
      return{
          test: [
         {"room_src":"http://admin.shangrongfintech.com/static/logo/upload/15260254195af54ccbd3915.png"},
        { "room_src":"http://admin.shangrongfintech.com/static/logo/upload/15259440195af40ed32fda8.png"},
          {"room_src":"http://admin.shangrongfintech.com/static/logo/upload/15260254195af54ccbd3915.png"},
       { "room_src":"http://admin.shangrongfintech.com/static/logo/upload/15228266965ac47dc81eb6a.png"},
       { "room_src":"http://admin.shangrongfintech.com/static/logo/upload/15232406015acace9914514.png"},
       { "room_src":"http://admin.shangrongfintech.com/upload/banner/5a2f9624e68fb.png"}
      ],
 swiperOption: {  
                //是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true  
                notNextTick: true, 
                autoplay:3000, 
                pagination: '.swiper-pagination',  
                slidesPerView: 'auto',  
                centeredSlides: true,  
                paginationClickable: true,  
                spaceBetween: 30,  
                    onSlideChangeEnd: swiper => {  
                        //这个位置放swiper的回调方法  
                        this.page = swiper.realIndex+1;  
                        this.index = swiper.realIndex;  
                    }  
                }  
            }  
        }
  }
</script>

<style lang="scss">
  .swiper-container {
    width: 100%;
    height: 10rem;
    .swiper-wrapper {
      width: 100%;
      height: 100%;
    }
    .swiper-slide {
      background-position: center;
      background-size: cover;
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
</style>


